import { Component, ViewChild, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ProductService } from "../../providers/product.services";
import { environment } from "../../../environments/environment";
import { ToastController } from '@ionic/angular';

import { ImageLoaderService } from 'ionic-image-loader';
import { ImageLoaderConfigService } from 'ionic-image-loader';

@Component({
  selector: 'page-wishList',
  templateUrl: 'wishList.html',
  styleUrls: ['./wishList.scss'],
})
export class WishListPage implements OnInit {


  public wishList: any=[];
  public cartCount = 0;
  public addTocartCss:Boolean= false;
  public searchWord:any
  public noRecordsFound: any = true;
  public isDataLoaded: Boolean = false;
  constructor(
    private _ProductService: ProductService,
    private toastController:ToastController,
   private imageLoader: ImageLoaderService,
   private imageLoaderConfig: ImageLoaderConfigService,
  ) {
    // this.imageLoaderConfig.setFallbackUrl('assets/img/1.png');
  }

  public isSearchEnable:Boolean=false;
public baseUrl = environment.baseUrl + '/';
public searchproductList:any=[]

   ngOnInit() {
    this.getwishList();
  }

  public showSearchBox: Boolean = false;
  enableSearch() {
    this.showSearchBox = true
  }

  backToNormal(){
    this.showSearchBox = false
  }

 public refershDefault = {
    target: {
      complete: function () {
        return true
      }
    }
  }
  getwishList(event = this.refershDefault) {
    // this.imageLoader.clearCache();
    let shopId = localStorage.getItem('shopID')
    this._ProductService.getAllWishList(localStorage.getItem('userId'),shopId).subscribe((data: any) => {
      console.log(data,"data")

      this.isDataLoaded = true
      if (data.length !== 0) {
      // setTimeout(()=>{
        this.wishList = data[0].productDetails;
        // this.wishList.forEach((cart) => {
        //   cart.price =
        //     this.convertIntoLakh(cart.productId.price)
        // });
      // },2000)
        console.log(this.wishList, "wishList")
        this.noRecordsFound = false
      }else{
        this.noRecordsFound = true
      }


    if (event) { }
      setTimeout(() => {
        console.log('Async operation has ended');
        event.target.complete();
      }, 1500);
    });
  }

  convertIntoLakh(value) {
    console.log(value,"value");
    let arr = value.split("-");
    console.log(arr,"arr");
    var v1:any =''
    var v2:any = ''
    if(arr.length == 2){
     v1 = Math.abs(arr[0]);
     v2 = Math.abs(arr[1]);
     v1 = (v1 / 100000).toFixed(2);
     v2 = (v2 / 100000).toFixed(2) ;
    }else{
     v1 = Math.abs(arr[0]);
     v1 = (v1 / 100000).toFixed(2);
     
    }
   
    let second = (v2)?" - "+v2:''
   return v1+ second + ' Lakh';
   }
   


  onImageLoad(event){
    console.log("loaded img",event)
  }

  removeToWishList(productData) {

    let payload = {
      productDetails: {
        productId: productData.productId._id,
      },
      UserId: localStorage.getItem('userId'),
      shopId: localStorage.getItem('shopID')
    }
    console.log(payload)
    this._ProductService.AddToWishCart('remove', payload).subscribe(async (data: any) => {
      if (data) {
        console.log(data, "data")
        this.getwishList()
      }
    });
  }

  moveTocart(cart) {
    let payload = {
      productDetails: {
        productId: cart.productId._id,
        productCount: 1,
      },
      UserId: localStorage.getItem("userId"),
      shopId: localStorage.getItem('shopID'),
    };
    this._ProductService
      .addToCart("add", payload)
      .subscribe(async (data: any) => {
        if (data) {
          this.addTocartCss = true;
          this.removeToWishList(cart)
          const toast = await this.toastController.create({
            message: "Moved To Cart",
            duration: 3000,
            position: "bottom",
            animated: true,
          });
          toast.present();
          this.getCartCount();
          setTimeout(function(){
            this.addTocartCss = false;
          },1000)
        }
      });
  }

  getCartCount(){
    let shopId = localStorage.getItem('shopID')
    var arr = [];
    this._ProductService
      .cartList(localStorage.getItem("userId"),shopId)
      .subscribe((data: any) => {
        if (data.length !== 0) {
          this.cartCount = data[0].productDetails.length
        }
      })
  }

}
